* {
  box-sizing: border-box;
}

body {
  background-color: #b5f5f1;
}

.header {
  width: 100%;
  height: 70px;
  background-image: linear-gradient(#b5f5f1, #05b3a8);
  justify-content: center;
  padding-top: 15px;
  padding-bottom: 15px;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  box-shadow: 0 4px 4px rgba(0, 0, 1, .5);
}

.form-input {
  height: 35px;
  text-align: center;
  color: #0c524e;
  background: #fff;
  border: 2px solid #05b3a8;
  border-radius: 6px;
  box-shadow: 0 4px 4px rgba(0, 0, 1, .5);
}

.form-input:focus {
  outline: none;
}

.form-btn {
  text-align: center;
  width: 80px;
  height: 35px;
  color: #fff;
  cursor: pointer;
  background: #05b3a8;
  border: none;
  border-radius: 6px;
  margin-left: 15px;
  box-shadow: 0 4px 4px rgba(0, 0, 1, .5);
}

.form-btn:focus, .form-btn:hover, .load-more:focus, .load-more:hover {
  background: #127a75;
}

.load-more {
  text-align: center;
  width: 80px;
  height: 35px;
  color: #fff;
  cursor: pointer;
  background: #05b3a8;
  border: none;
  border-radius: 6px;
  box-shadow: 0 4px 4px rgba(0, 0, 1, .5);
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 100px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

.photo-card {
  width: 350px;
  flex-direction: column;
  margin-bottom: 30px;
  margin-right: 30px;
  display: flex;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.photo-link {
  height: 240px;
}

.photo-image {
  height: 240px;
  width: 100%;
  object-fit: cover;
}

.info {
  height: 80px;
  background-color: #fff;
  justify-content: space-around;
  display: flex;
}

.info-item {
  flex-direction: column;
  align-items: center;
  font-size: 14px;
  display: flex;
}

